<template>
  <div class="home">
    <home-swiper class="home__swiper" ref="swiper"></home-swiper>
    <p class="little_title"></p>
    <list :parent="parent"></list>
    <!-- <footer-component :link-actived="linkActived"></footer-component> -->
  </div>
</template>

<script>
import FooterComponent from './common-components/footer'
import HomeSwiper from './home-components/home-swiper'
import List from './common-components/list'

export default {
  name: 'home',
  data () {
    return {
      linkActived: '/',
      parent:"Home"
    }
  },
  components: { FooterComponent, HomeSwiper, List },
   created: function(){
     var that = this;
     this.axios.get("/home").then((res) => {
       console.log(res)
     }).catch(function (err) {
       console.log("");
       that.$router.push({name: 'Login'});
     })
   },

  mounted: function(){
    this.$refs.swiper.$el.style.height = screen.width/1.7+'px';
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
.home{
  background: #fafafa;
  padding-top: 8px;
  padding-bottom: 1px;
}

.home__swiper{
  margin-top: -8px;
  /*border-radius: 8px;*/
  /*width: calc(100% - 40px);*/
  margin-bottom: 18px;
  box-shadow: 0px 0px 20px #d4d4d4;

}

  p.little_title{
    color: #666666;
    font-family: "PingFang SC";
    position: relative;
    top: -10px;
    display: inline-block;
    width: 85%;
    border-left: 10px solid dodgerblue;
    padding-left: 20px;
    text-align: left;
    font-size: 14px !important;
    font-weight: 600;
    margin-bottom: 5px;
  }
</style>



