<template>
  <!--<div class="layer">-->
     <!--<img class="login__img" src="../assets/logo.png" alt="logo" >-->
    <div class="login__container">
      <mt-popup
        v-model="popVisible"
        position="top"
        :modal=false
        class="popUp"
        v-html="popInfo"
      ></mt-popup>
      <!--<div class="login__choose">-->
        <!--<div @click="logIn" :class="['login__choose__item','login__choose__item1', choose == ''? 'login__choose__item-active':'']"> </div>-->
        <!--<div @click="signUp" :class="['login__choose__item','login__choose__item2', choose == ''? 'login__choose__item-active':'' ]"> </div>-->
      <!--</div>-->
      <img src="../assets/logo_02.png" alt="logo" style="height: 100px;margin-top: 5vh; ">
        <h2 v-if="choose === ''" class="headInfo"></h2>
        <h2 v-if="choose === ''" class="headInfo"></h2>
      <form class="login__form">
        <div class="inputform">
          <!--<label for="userId">ID</label>-->
          <input v-model="userID" class="login__input" type="text" placeholder="ID" id="userId">
        </div>
        <div class="inputform">
          <!--<label for="password"></label>-->
          <input v-model="userPassword" class="login__input" type="password" placeholder=" " id="password">
        </div>
        <div class="inputform" v-if="choose === ''">
          <!--<label for="username"></label>-->
          <input  v-model="userName" class="login__input" type="text" placeholder="" id="userName">
        </div>
        <div v-if="choose === ''" class="inputform">
          <!--<label for="type"></label>-->
          <select v-model="userType" class="login__input" id="type" style="background: none;">
            <option value="" disabled selected></option>
            <option value="0"></option>
            <option value="1"></option>
          </select>
        </div>
        <div @click="submit" class="login__submit">{{choose}}</div>
        <div class="tipsInfo" v-if="choose === ''"><span class="tips">? <a @click="signUp" style="color: dodgerblue"> </a></span></div>
        <div class="tipsInfo" v-if="choose === ''"><span class="tips">? <a @click="logIn" style="color: dodgerblue"> </a></span></div>

      </form>
    </div>
  <!--</div>-->
</template>

<script>
  import ElFormItem from "../../node_modules/element-ui/packages/form/src/form-item.vue";
  export default {
  name: 'login',
  data: function(){
    return {
      choose : '',
      userID : '',
      userPassword : '',
      userType:"",
      userName:"",
      popInfo:"!",
      popVisible:false
    }
  },
  methods: {
    logIn: function(){
      this.choose = '';
    },
    signUp: function(){
      this.choose = '';
    },
    submit: function(){
      var that = this;
      if(this.choose === ""){
        this.axios.post("/login",{
          userId: this.userID,
          userPassword: this.userPassword
        }).then(function(res){
          console.log(res);
          if(res.data==="success"){
            that.$emit('finishLogin');
            that.$cookies.set("status","alive",60*20);
            that.$router.push({name: 'User'});
          }
        }).catch(function (err) {
          that.popInfo = "!";
          that.popVisible = true;
          setTimeout(function () {
            that.popupVisible = false;
            that.$router.push({name: 'Home'});
          },1000);
        });
      }else{
        this.axios.post("/signUp",{
          userId: this.userID,
          userPassword: this.userPassword,
          userType: this.userType,
          userName: this.userName,
        }).then(function(res) {
          if (res.status === 200) {
            that.popInfo = ",\"" + "<span style='color: red;font-size: 16px'>" + res.data.password + "</span>" + "\",";
            that.popVisible = true;
//            alert(res.data.password);
            setTimeout(() => {
              that.$router.push({name: 'Home'});
            }, 1000)
          }
        }).catch(function (err) {
          if(err.response.data==="failed signup"){
            that.popInfo = "!";
            that.popVisible = true;
            setTimeout(function () {
              that.popupVisible = false;
              that.$router.push({name: 'Home'});
            },1000);


          }
        })
      }
    }
  },
    components:{
      ElFormItem
    }
}
</script>

<style scoped>
  .login__container{
    height: 83.5vh;
    font-family: "PingFang SC","Microsoft YaHei UI";
  }
  .login__choose{
    width: 100%;
    position: relative;
    /*top: -10px;*/
  }
  .login__choose div{
    /*box-sizing: border-box;*/
    /*box-shadow: -1px -1px 3px #d9d9d9;*/
    text-align: center;
    display: inline-block;
    width: 48.5%;
    border-bottom: 3px solid #eef2f7;
    font-size: 20px;
    color: #666666;
    /*height: 28px;*/
  }
  .login__choose div.login__choose__item-active{
    border-bottom: 3px solid #409eff !important;
    color: #409eff;
  }
  .headInfo{
    padding-top: 10px;
    color: dodgerblue;
    font-size: 30px;
  }
  .login__input{
    outline-color: #6ec0ff;
    -moz-outline-radius: 3px;
    margin: 0 auto;
    border-radius: 3px;
    width: 250px;
    /*border: 1px solid gainsboro;*/
    border: 1px solid #c8d9ef;
    height: 30px;
    line-height: 30px;
    font-family: "PingFang SC","Inziu Iosevka Slab SC","Microsoft YaHei UI";
    text-indent: 5px;
    color: #1c8de0;
    margin-top: 20px;
    /*text-align: center;*/
    /*display: block;*/
    /*margin-top: 1rem;*/
    /*margin-bottom: 1rem;*/
  }
  .login__input::placeholder{
    font-family: "PingFang SC","Inziu Iosevka Slab SC","Microsoft YaHei UI";
    color: #cdcdcd;
    padding-left: 5px;
  }
  .login__form{
    margin: 0 auto;
    width: 90%;
    /*border: 1px solid gainsboro;*/
    border-radius: 5px;
  }
  .inputform label{
    height: 32px;
    position: relative;
    top: 1px;
    line-height: 30px;
    display: inline-block;
    font-family: "PingFang SC","Inziu Iosevka Slab SC","Microsoft YaHei UI";
    text-align: left !important;
    width: 60px !important;
    font-size: 16px;
  }

  .login__submit{
    margin-top: 20px;
    font-size: 20px;
    line-height: 35px;
    display: inline-block;
    width: 254px;
    /*margin-left: 72px;*/
    background: #409eff;
    /*border: 1px solid #8c8c8c;*/
    height: 35px;
    border: none;
    border-radius: 5px;
    color: white;
  }
  .login__submit:hover{
  background-color: #57bfff;
  }
  .tipsInfo{
    height: 40px;
    /*margin-top: 0;*/
  }
  .tips{
    position: relative;
    /*top: -px;*/
    /*line-height: 30px;*/
    font-size: 16px;
    color: #a1a1a1;
  }

  .popUp{
    font-family: "PingFang SC";
    background-color: rgba(0, 0, 0, 0.62);
    color: rgba(255, 255, 255, 0.63);
    width: 100% !important;
    height: 50px!important;
    line-height: 50px !important;
    text-align: center;
    font-size: 14px;
  }



</style>



